import React, { Component } from "react";
import "./App.css";
import { Switch, Route, Redirect } from "react-router-dom";
import Navbar from "./Component/Navbar";
import Footer from "./Component/Footer";
import SignInPage from "./Component/SignInPage";
import ArticlePage from "./Component/ArticlePage";
import SubscribePage from "./Component/SubscribePage";
import HomePage from "./Component/HomePage";
import CO2Page from "./Component/CO2Page";
import TempPage from "./Component/TempPage";
import GlacierPage from "./Component/GlacierPage";
import SeaPage from "./Component/SeaPage";

class App extends Component {
  //sätter state med olika värden.
  state = {
    CO2Emission: [],
    GlobalTemp: [],
    GlacierSize: [],
    SeaLevel: [],
    selectedData: "Choose data...",
    filteredCO2Emission: [],
    filteredGlobalTemp: [],
    filteredGlacierSize: [],
    filteredSeaLevel: [],
    showCO2Emission: false,
    showGlobalTemp: false,
    showGlacierSize: false,
    showSeaLevel: false,
    indexYear: 0,
  };

  async componentDidMount() {
    //Hämtar från data från API med Fossil Fuels
    const CO2url = "https://my.api.mockaroo.com/co2.json?key=8eb9e6f0";
    const CO2response = await fetch(CO2url);
    const CO2data = await CO2response.json();

    //Hämtar data från API Global Temperature
    const GlobalTempurl = "https://my.api.mockaroo.com/temp.json?key=8eb9e6f0";
    const GlobalTempresponse = await fetch(GlobalTempurl);
    const GlobalTempdata = await GlobalTempresponse.json();

    //Hämtar data från API Glacier size
    const GlacierSizeurl =
      "https://my.api.mockaroo.com/glaciersize.json?key=8eb9e6f0";
    const GlacierSizeresponse = await fetch(GlacierSizeurl);
    const GlacierSizedata = await GlacierSizeresponse.json();

    //Hämtar data från API Sea Level
    const SeaLevelurl =
      "https://my.api.mockaroo.com/sealevel.json?key=8eb9e6f0";
    const SeaLevelresponse = await fetch(SeaLevelurl);
    const SeaLeveldata = await SeaLevelresponse.json();
// sätter state på olika variabler
    this.setState({
      CO2Emission: CO2data,
      GlobalTemp: GlobalTempdata,
      GlacierSize: GlacierSizedata,
      SeaLevel: SeaLeveldata,
    });
  }
// Hämtar ut värdet som användaren valt i drop-down funktionen på "Article Page"
  handleSetDataChange = (e) => {
    this.setState({ selectedData: e.target.value });
  };

/*Skapar en funktion som kollar att om t.ex. CO2Emission har valts av användaren i drop-down 
funktionen och att årtal som finns i API har skrivits in, så ska tabellen med data från CO2Emission
visas. Om användaren inte skulle skriva in några årtal och försöker söka på data så kommer det 
komma ett felmeddelande som berättar för användaren att det måste fyllas i årtal.*/
  handleYearFilter = (yearFrom, yearTo) => {
    let selectedData = this.state.selectedData;

    if (selectedData === "CO2Emission") {
      if (yearFrom.length === 0 && yearTo.length === 0) {
        alert("Please enter two years between 2000 and 2019");
        return;
      }
      this.filterCO2Emission(yearFrom, yearTo);
    }
    if (selectedData === "GlacierSize") {
      if (yearFrom.length === 0 && yearTo.length === 0) {
        alert("Please enter two years between Year and Year");
        return;
      }
      this.filterGlacierSize(yearFrom, yearTo);
    }
    if (selectedData === "GlobalTemperature") {
      if (yearFrom.length === 0 && yearTo.length === 0) {
        alert("Please enter two years between Year and Year");
        return;
      }
      this.filterGlobalTemp(yearFrom, yearTo);
    }
    if (selectedData === "SeaLevel") {
      if (yearFrom.length === 0 && yearTo.length === 0) {
        alert("Please enter two years between Year and Year");
        return;
      }
      this.filterSeaLevel(yearFrom, yearTo);
    }
  };

  /*Här skapas filtreringen av de olika data som finns i den givna API.*/
  filterCO2Emission(yearFrom, yearTo) {
    let filtered = this.state.CO2Emission;
    filtered = filtered.filter(
      (data) => data.Year >= parseInt(yearFrom) && data.Year <= parseInt(yearTo)
    );
    this.setState({ filteredCO2Emission: filtered });
    this.setState({ showCO2Emission: true });
  }
  filterGlacierSize(yearFrom, yearTo) {
    let filtered = this.state.GlacierSize;
    filtered = filtered.filter(
      (data) => data.Year >= parseInt(yearFrom) && data.Year <= parseInt(yearTo)
    );
    this.setState({ filteredGlacierSize: filtered });
    this.setState({ showGlacierSize: true });
  }
  filterGlobalTemp(yearFrom, yearTo) {
    let filtered = this.state.GlobalTemp;
    filtered = filtered.filter(
      (data) => data.Year >= parseInt(yearFrom) && data.Year <= parseInt(yearTo)
    );
    this.setState({ filteredGlobalTemp: filtered });
    this.setState({ showGlobalTemp: true });
  }
  filterSeaLevel(yearFrom, yearTo) {
    let filtered = this.state.SeaLevel;
    filtered = filtered.filter(
      (data) =>
        parseInt(data.Time.substring(0, 5)) >= parseInt(yearFrom) &&
        parseInt(data.Time.substring(0, 5)) <= parseInt(yearTo)
    );
    this.setState({ filteredSeaLevel: filtered });
    this.setState({ showSeaLevel: true });
  }


  //Här kollar vi om användarinput (år) finns i CO2 data,
  //Om året finns så tilldelar vi indexen för året i state variabeln indexYear, om inte ett felmeddelande
  handleSendYear = (year) => {
    let index = this.state.CO2Emission.findIndex((co2) => co2.Year === parseInt(year));
    if (index !== -1) {
      this.setState({ indexYear: index });
    } else {
      alert("Please enter a year between 2000 and 2019");
    }
  };

// renderar allt innehåll som komponenter och länkar som ska presenteras i applikationer 
  render() {
    return (
      <div className="App">
        <Navbar />

        <Switch>
          <Route path="/homepage" render={(props) => <HomePage {...props} />} />

          <Route
            path="/subscribe"
            render={(props) => <SubscribePage {...props} />}
          />

          <Route path="/signin" render={(props) => <SignInPage {...props} />} />

          <Route
            path="/article"
            render={(props) => (
              <ArticlePage {...props} 
              // sätter state på variablen
              selectedData={this.state.selectedData} 
              onSetDataChange={this.handleSetDataChange}
              onYearFilter={this.handleYearFilter}
              showCO2Emission={this.state.showCO2Emission}
              showGlobalTemp={this.state.showGlobalTemp}
              showGlacierSize={this.state.showGlacierSize}
              showSeaLevel={this.state.showSeaLevel}
              filteredCO2Emission={this.state.filteredCO2Emission}
              filteredGlobalTemp={this.state.filteredGlobalTemp}
              filteredGlacierSize={this.state.filteredGlacierSize}
              filteredSeaLevel={this.state.filteredSeaLevel}
              />
            )}
          />

          <Route
            path="/co2emission"
            render={(props) => (
              <div>
              <CO2Page
                {...props}
                CO2Emissiondata={this.state.CO2Emission[this.state.indexYear]} 
                onSendYear={this.handleSendYear} 
              />
              </div>
            )}

          />
          
          <Route
            path="/globaltemperature"
            render={(props) => (

            <TempPage {...props} tempdata={this.state.GlobalTemp} />
             
            )}
          />

          <Route
            path="/glaciersize"
            render={(props) => (
            <div>
            <GlacierPage {...props} 
                glacierSizedata={this.state.GlacierSize} 
              
            />


            </div>
            )}
          />


          <Route path="/sealevel" render={(props) => 
          <SeaPage {...props} seaLeveldata={this.state.SeaLevel}/>
          
          } />

          <Redirect from="/" exact to="/article"></Redirect>
        </Switch>

        <Footer />
      </div>
    );
  }
}

export default App;
