// importerar alla nödvändiga komponenter i sidan.
import React, { Component } from "react";
import Filter from "./Filter";
import CO2EmissionTable from "./CO2EmissionTable";
import GlobalTempTable from "./GlobalTempTable";
import GlacierSizeTable from "./GlacierSizeTable";
import SeaLevelTable from "./SeaLevelTable";
import { Link } from "react-router-dom";
import Backbutton from "./Backbutton";

class ArticlePage extends Component {
  // båda handle sätter prop från app.js
  handleDataChange = (e) => {
    this.props.onSetDataChange(e);
  };
  handleSendYears = (yearFrom, yearTo) => {
    this.props.onYearFilter(yearFrom, yearTo);
  };

  render() {
    /*Här väljs det vilken tabell som ska visas baserat på användarens sökning*/
    let table;
    if (this.props.showCO2Emission) {
      table = <CO2EmissionTable CO2Emission={this.props.filteredCO2Emission} />;
    }
    if (this.props.showGlobalTemp) {
      table = <GlobalTempTable GlobalTemp={this.props.filteredGlobalTemp} />;
    }
    if (this.props.showGlacierSize) {
      table = <GlacierSizeTable GlacierSize={this.props.filteredGlacierSize} />;
    }
    if (this.props.showSeaLevel) {
      table = <SeaLevelTable SeaLevel={this.props.filteredSeaLevel} />;
    }

    /*Här skapar vi en informationsruta*/
    return (
      <div>
        <div className='container-sm article-image'>
          <h1 className='bottom-left'>Climate Change</h1>
        </div>
        <div className='container'>
          <h2 className='functionname'>Fashion's Environmental Impact!</h2>
          <div className='waveimg'></div>
          <p className='text articletext'>
            For several years, the fashion industry has been criticized for its
            unsustainable behavior, which has contributed to a negative impact
            on human well-being and the quality of the environment. This is due
            to high levels of carbon dioxide emissions, excessive use of
            chemicals and increased waste.
          </p>
          <p>
            The industry has a serious bad impact on our the planet and our
            enviroment. The fashion industry is in fact the second largest
            polluter in the world, and the damage on the planet is rapidly
            increasing as the fashion industry is still growing and becoming
            bigger and bigger.
          </p>
        </div>
        <div className='boxBorder'>
          <div className=' container info'>
            <h3 className='functionname'>
              Compare the climate change through different years in our table
            </h3>

            <div className='waveimg'></div>
            <p className='articletext'>
              Below you can compare data between different years, just write in
              which year you want to start with and end with another year. For
              best comparison, write for two years that have an annual interval.
            </p>
          </div>
          {/* här används filter komponent för att filtrerar innehållet */}
          <Filter
            selectedData={this.props.selectedData}
            onDataChange={this.handleDataChange}
            onSendYears={this.handleSendYears}
          />
          {table}
        </div>

        {/*Här skapar vi knappar som leder till sidorna med diagrammen genom routing*/}
        <div className='container-sm p-3 mb-3 centerbtn'>
          <div className='waveimg2'></div>
          <h5 className='mt-3'>
            Explore how the climate have changed with our diagrams
          </h5>
          <Link className='explore' to='co2emission'>
            <button className='btndiagram'>CO2 Emission</button>
          </Link>

          <Link className='explore' to='globaltemperature'>
            <button className='btndiagram'>Global temperature</button>
          </Link>

          <Link className='explore' to='glaciersize'>
            <button className='btndiagram'>Glacier size</button>
          </Link>

          <Link className='explore' to='sealevel'>
            <button className='btndiagram'>Sea level</button>
          </Link>
        </div>

        <div className='container backCenter'>
          <Link className='link' to='./homepage'>
            <Backbutton></Backbutton>
          </Link>
        </div>
      </div>
    );
  }
}

export default ArticlePage;
