// importerar alla nödvändiga komponenter i sidan.
import React, { Component} from "react";
import Backbutton from "./Backbutton"
import { Link } from "react-router-dom";

class SignInPage extends Component {
  state = {};
  // renderar det som ska presenteras i applikaitonen.
  // Använder bootstraps "form" för att skapa en layout på sidan
  render() {
    return (
      <div className='container'>
        <form className="signinborder">
        <div className="form-row signin">
        <h1 className="title">Cosmopolitan</h1>
         <div className="form-group col-md-6 mb-2 mt-3" >
         <label for="inputEmail4">E-mail</label>
         <input type="email" className="form-control" id="inputEmail4" placeholder="Email"/>
         </div> 
          <div className="form-group col-md-6">
          <label for="inputPassword4">Password</label>
          <input type="password" className="form-control" id="inputPassword4" placeholder="Password"/>
          <div className='centersignin'>
          <button type="submit" className="Signinbutton">Sign in</button> 
          <div>
          <a className="forgot">Forgot password?</a>
          </div>
          </div>
        </div>
      </div>
      </form>
      <div className="memberborder">
      <p>Don't you have an account? <br></br><a className="member">Become a member</a></p>
      </div>
      <div className='container backCenter'>
      <Link className="link" to="./homepage">
          <Backbutton></Backbutton>
        </Link>
        </div>
      </div>
      
    );
  }
}

export default SignInPage;
