// importerar alla nödvändiga komponenter i sidan.
import React, { Component } from "react";
import { PieChart, Pie, Legend, Tooltip, Cell } from "recharts";
import { Link } from "react-router-dom";
import { IoIosArrowBack, IoIosArrowForward } from "react-icons/io";
import Backbutton from "./Backbutton";

// lägger in en tom state för att användaren ska kunna skriva in åren senare i sökfunktionen
class CO2Page extends Component {
  state = {
    inputYear: " ",
  };

  //här ser vi till att användarinput skickas till props onSendYear i App.js när användare klickar på enter
  handleYear = (e) => {
    this.setState({ inputYear: e.target.value });
  };

  render() {
    let CO2 = this.props.CO2Emissiondata; //lagra CO2data från App.js i variabeln CO2

    if (CO2 === undefined) return <p>Please wait a few seconds...</p>;

    //tilldela olika properties i property value
    const CO2PieChart = [
      { name: "Gas Fuel", value: CO2["Gas Fuel"] },
      { name: "Liquid Fuel", value: CO2["Liquid Fuel"] },
      { name: "Solid Fuel", value: CO2["Solid Fuel"] },
      { name: "Cement", value: CO2["Cement"] },
    ];

    const COLORS = [" #cc7a00", " #ffa64d", "#ff751a", "#ff9966"];

    return (
      <div className='container'>
        <div className='textbox'>
          <h4>Global CO2 Emission</h4>
          <div className='waveimg'></div>
          <p className='textboxpadding'>
            Carbon dioxide emissons or CO2 emissons is the chemical formula for
            carbon dioxide. The letter C stands for the element carbon and the
            letter O is the symbol for oxygen. The release of the greenhouse
            gases is called emissons.
          </p>
          <p>
            CO2 is necessary for the life on earth. Without CO2 humans wouldn't
            be able to breathe. Carbon dioxide also helps the earth to hold the
            energy it recives from the sun. If it weren’t for carbon dioxide,
            The oceans on earth would be solid frozen.
          </p>
        </div>
        <div>
          <input
            className='p-2 m-2'
            type='text'
            placeholder='Enter Year...'
            onChange={(e) => this.handleYear(e)} //hantera event objekt i funktionen handleYear
          />
          <button
            type='button'
            className='filter-btn'
            onClick={() => this.props.onSendYear(this.state.inputYear)}
          >
            See diagrams
          </button>
        </div>
        <PieChart width={400} height={420} className='centerDiagram'>
          <Pie
            dataKey='value'
            isAnimationActive={false}
            data={CO2PieChart}
            cx={200}
            cy={200}
            outerRadius={80}
            fill='#8884d8'
            label
          >
            <Cell fill={COLORS[0]} />
            <Cell fill={COLORS[1]} />
            <Cell fill={COLORS[2]} />
            <Cell fill={COLORS[3]} />
          </Pie>

          <Legend layout='horizontal' />
          <Tooltip />
        </PieChart>
        <div className='backnext'>
          <Link className='link text-left' to='/sealevel'>
            <IoIosArrowBack />
            Previous diagram{" "}
          </Link>
          <Link className='link text-right' to='/globaltemperature'>
            Next diagram <IoIosArrowForward />
          </Link>
        </div>

        <div className='row mt-3'>
          <div className='card col box1'>
            <div className='card-header box1Header'>Gas Fuel</div>
            <div className='card-body box1Body'>
              <p className='card-text boxText'>
                Natural gas is a fossil energy source that formed deep beneath
                the earth's surface.
              </p>
              <br />
              <p className='boxText'>The unit is measured in kilogram (kg).</p>
            </div>
          </div>

          <div className='card col box2'>
            <div className='card-header box2Header'>Liquid Fuel</div>
            <div className='card-body box2Body'>
              <p className='card-text boxText'>
                Liquefied gas is a gas that has been turned into a liquid by
                cooling or compressing it.
              </p>
              <br />
              <p className='boxText'>The unit is measured in kilogram (kg).</p>
            </div>
          </div>
        </div>

        <div className='row mt-3'>
          <div className='card col box3'>
            <div className='card-header box3Header'>Solid Fuel</div>
            <div className='card-body box3Body'>
              <p className='card-text boxText'>
                Solid fuel refers to various forms of solid material that can be
                burnt to release energy.
              </p>
              <br />
              <p className='boxText'>The unit is measured in kilogram (kg).</p>
            </div>
          </div>

          <div className='card col box4'>
            <div className='card-header box4Header'>Cement</div>
            <div className='card-body box4Body'>
              <p className='card-text boxText'>
                If the cement industry were a country, it would be the third
                largest emitter in the world.
              </p>
              <br />
              <p className='boxText'>The unit is measured in kilogram (kg).</p>
            </div>
          </div>
        </div>

        <div className='container backCenter'>
          {/* använder link för att användaren ska kunna navigera tillbaka till hemsida */}
          <Link className='link' to='./homepage'>
            <Backbutton></Backbutton>
          </Link>
        </div>
      </div>
    );
  }
}

export default CO2Page;
