import React, { Component } from "react";
import { Link } from "react-router-dom";

class CO2EmissionTable extends Component {
// sätter props med GlacierSize api på en varibel.
  render() {
    let CO2EmissionData = this.props.CO2Emission;

    return (
      /*Skapar en tabell med 3 kolumner som kommer innehålla data från API. 
    Vi hämtar och mappar datan, och lägger sedan in den hämtade datan 
    i den skapade tabellen*/

    /*Skapar även en informationsruta som innehåller förklaringar och fakta
    om den hämtade datan*/
      <div>
        <table className="table table-bordered table-responsive">
          <thead>
            <tr>
              <th scope="col">Year</th>
              <th scope="col">Total</th>
              <th scope="col">Gas Fuel</th>
              <th scope="col">Liquid Fuel</th>
              <th scope="col">Solid Fuel</th>
              <th scope="col">Cement</th>
            </tr>
          </thead>
          <tbody>
            {CO2EmissionData.map((CO2) => (
              <tr>
                <td>{CO2.Year}</td>
                <td>{CO2.Total}</td>
                <td>{CO2["Gas Fuel"]}</td>
                <td>{CO2["Liquid Fuel"]}</td>
                <td>{CO2["Solid Fuel"]}</td>
                <td>{CO2["Cement"]}</td>
              </tr>
            ))}
          </tbody>
        </table>

        <div className="tableinfo" r>
        <p className="titletable">What does it mean?</p>
        <p className="unit"><b>Unit: </b> Kilogram (kg)</p>
          <p><b>Gas Fuel:</b> Natural gas is a fossil energy source that formed deep beneath the earth's surface. </p>
          <p><b>Liquid Fuel:</b> Liquefied gas is a gas that has been turned into a liquid by cooling or compressing it </p>
          <p><b>Solid Fuel:</b> Solid fuel refers to various forms of solid material that can be burnt to release energy.</p>
          <p><b>Cement: </b> If the cement industry were a country, it would be the third largest emitter in the world.</p>
        </div>
      </div>
    );
  }
}

export default CO2EmissionTable;
