import React, { Component } from "react";
import { Link } from "react-router-dom";
import "../App.css";
import { FaLock } from "react-icons/fa";

class Navbar extends Component {
  render() {

    /*En färdig navbar hämtades genom bootstrap, men som vi sedan designade
    om för att passa in i vår applikation. Vi lade till länkar som vi routat
    till andra skapade sidor och skapade även "låsta" länkar för att visa
    att användaren inte prenumererar och har därför inte tillgång till 
    hela applikationen.*/
    return (
      <div className="container">
        <nav
          className="navbar navbar-toggleable-md navbar-light"
          id="commRollover"
        >
          <button
            className="navbar-toggler navbar-toggler-right"
            type="button"
            data-toggle="collapse"
            data-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <Link className="navbar-brand logo" to="/homepage">
            Cosmopolitan
          </Link>

          <Link to="/subscribe">
            <button type="button" className="subscribe-btn">
            SUBSCRIBE
            </button>
          </Link>
          <Link className="sign-in link sign" to="/signin">
            Sign in
          </Link>

          <div
            className="collapse navbar-collapse justify-content-end"
            id="navbarNav"
          >
            <ul className="navbar-nav">
              <li className="nav-item active">
                <Link className="nav-link active" to="/article">
                  Climate Change
                  <span className="sr-only"></span>
                </Link>
              </li>
              <li className="nav-item active">
                <Link className="nav-link inactive" to="#">
                  Fashion <FaLock /> <span className="sr-only"></span>
                </Link>
              </li>
              <li className="nav-item active">
                <Link className="nav-link inactive" to="#">
                  Cosmetics <FaLock />
                  <span className="sr-only"></span>
                </Link>
              </li>
              <li className="nav-item active">
                <Link className="nav-link inactive" to="#">
                  Celebrity Gossips <FaLock />
                  <span className="sr-only"></span>
                </Link>
              </li>
            </ul>
          </div>
        </nav>
      </div>
    );
  }
}

export default Navbar;
