import React, { Component, Fragment } from "react";
import {
  FaFacebookF,
  FaTwitter,
  FaPinterestP,
  FaYoutube,
  FaInstagram,
  FaTumblr,
} from "react-icons/fa";

class Footer extends Component {
  state = {};
  render() {

/*Här skapas en footer som innehåller företagets logga, sociala medie ikoner samt en
informationstext. Ikonerna är klickbara och länkar till företagets sociala medier.
Ikonerna är färdiga och hämtade från react.*/
    return (
      <Fragment>
        <div className="container footer">
          <a className="navbar-brand logo" href="#">
            Cosmopolitan
          </a>
          <div className="Social">
            <a
              className="ikon"
              href="https://www.facebook.com/Cosmopolitan"
              target="_blank"
            >
              <FaFacebookF />
            </a>
            <a
              className="ikon"
              href="https://twitter.com/Cosmopolitan"
              target="_blank"
            >
              <FaTwitter />
            </a>
            <a
              className="ikon"
              href="https://www.pinterest.se/Cosmopolitan/"
              target="_blank"
            >
              <FaPinterestP />
            </a>
            <a
              className="ikon"
              href="https://www.instagram.com/cosmopolitan/"
              target="_blank"
            >
              <FaInstagram />
            </a>
            <a
              className="ikon"
              href="https://www.youtube.com/cosmopolitan"
              target="_blank"
            >
              <FaYoutube />
            </a>
            <a
              className="ikon"
              href="https://cosmopolitanmagazine.tumblr.com/"
              target="_blank"
            >
              <FaTumblr />
            </a>
          </div>
          <div className="footertext">
            <p> A Part of Hearst Digital Media</p>
            <p>
              Cosmopolitan participates in various affiliate marketing programs,
              which means we may get paid commissions on editorially chosen
              products purchased through our links to retailer sites. ©2020
              Hearst Magazine Media, Inc. All Rights Reserved.
            </p>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default Footer;
