// importerar alla nödvändiga komponenter i sidan.
import React, { Component } from "react";
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
  Legend,
} from "recharts";
import { Link } from "react-router-dom";
import { IoIosArrowBack, IoIosArrowForward } from "react-icons/io";
import Backbutton from "./Backbutton";

// lägger in en tom state för att användaren ska kunna skriva in åren senare i sökfunktionen
class TempPage extends Component {
  state = {
    inputYear: " ",
  };

  handleYear = (e) => {
    this.setState({ inputYear: e.target.value });
  };

  // renderar det som ska presenteras i applikaitonen.
  render() {
    // lägger in tempdatan från apiet.
    let temp = this.props.tempdata;
    // om apiet inte läser in får användaren ett meddelande.
    if (temp === undefined) return <p>Please wait a few seconds...</p>;

    return (
      // Använder boostrap och läggar allt i en container.
      //Använder färdiga diagram/komponenter för att presentera data från apiet i diagramen.
      // Använder ikoner från react-icons
      <div className='container'>
        <div className='textbox '>
          <h4>Global Temperature</h4>
          <div className='waveimg'></div>
          <p className='textboxpadding'>
            <p>
              As the atmosphere of earth heats up the higher the temperaure
              gets. The consequence of higher temperarue will increase to more
              types of disasters such as storms, foods, heat waves and melting
              glaciers.
            </p>
            According to an ongoing temperature analysis conducted by scientists
            at NASA’s Goddard Institute for Space Studies (GISS), the average
            global temperature on Earth has increased by a little more than 1°
            Celsius
          </p>
          <p>As the earth's atmosphere heats up </p>
          <p>1 degree Celsius = 33.8 degree Fahrenheit</p>
          <div className='instructionbox'>
            <p className='instruction'>
              Down below you can drag on the diagram to select which year you
              like to look at
            </p>
          </div>
        </div>

        <div className='tempchart' style={{ width: "100%", height: 300 }}>
          <ResponsiveContainer>
            <AreaChart
              data={temp}
              margin={{
                top: 10,
                right: 30,
                left: 0,
                bottom: 0,
              }}
            >
              <CartesianGrid strokeDasharray='3 3' />
              <XAxis dataKey='Year' />
              <YAxis />
              <Legend />
              <Tooltip />
              <Area
                type='monotone'
                dataKey='Mean'
                stroke='#FF5500'
                fill='#ffbf80'
              />
            </AreaChart>
          </ResponsiveContainer>
        </div>
        <div className='backnext'>
          <Link className='link text-left' to='/co2emission'>
            <IoIosArrowBack />
            Previous diagram
          </Link>
          <Link className='link text-right' to='/glaciersize'>
            Next diagram
            <IoIosArrowForward />
          </Link>
        </div>
        <div className='card col box7'>
          <div className='card-header box7Header'>Mean?</div>
          <div className='card-body box7Body'>
            <p className='card-text boxText'>
              The average temperature of the air based upon hourly observations
              of thermometer during a given time period, usually a day, a month,
              or a year at a given place.
            </p>
            <br />
            <p className='boxText'>The mean is measured in Fahrenheit.</p>
          </div>
        </div>
        <div className='container backCenter'>
          <Link className='link' to='./homepage'>
            <Backbutton></Backbutton>
          </Link>
        </div>
      </div>
    );
  }
}

export default TempPage;
