import React, { Component } from "react";

class GlobalTempTable extends Component {
  render() {
    // sätter props med globaltemp api på en varibel.
    let GlobalTempData = this.props.GlobalTemp;
    return (
       /*Skapar en tabell med 3 kolumner som kommer innehålla data från API. 
    Vi hämtar och mappar datan, och lägger sedan in den hämtade datan 
    i den skapade tabellen*/

    /*Skapar även en informationsruta som innehåller förklaringar och fakta
    om den hämtade datan*/
      <div>
        <table className='table table-bordered table-responsive'>
          <thead>
            <tr>
              <th scope='col'>Year</th>
              <th scope='col'>Mean</th>
              <th scope='col'>Source</th>
            </tr>
          </thead>
          <tbody>
            {GlobalTempData.map((temp) => (
              <tr key={temp.Year}>
                <td>{temp.Year}</td>
                <td>{temp.Mean}</td>
                <td>{temp.Source}</td>
              </tr>
            ))}
          </tbody>
        </table>
        <div className="tableinfo">
        <p className="titletable">What does it mean?</p>
        <p className="unit"><b>Unit:</b> Fahrenheit (F)</p>
          <p><b>Mean:</b> The average temperature of the air based upon hourly observations of thermometer during a given time period, usually a day, a month, or a year at a given place. The mean is measured in Fahrenheit.</p>
          <p><b>Source:</b> The observation station for measuring the air temperature.</p>
        </div>
      </div>
    );
  }
}

export default GlobalTempTable;
