import React, { Component } from "react";
import pil from "../piljavel.png";

class Filter extends Component {
  state = {
    yearFrom: " ",
    yearTo: " ",
  };
  handleYearFrom = (e) => {
    this.setState({ yearFrom: e.target.value });
  };
  handleYearTo = (e) => {
    this.setState({ yearTo: e.target.value });
  };

  render() {

    /*Här skapas själva filterstrukturen med en drop-down funktion som innehåller
     olika alternativ, inputs där användaren skriver in årtalen och en knapp.*/
    return (
      <div className="container">
        <label>
          <div className="row">
            <div className="col">
              <p>
                Pick type of <span className="datatype">data:</span>
              </p>
            </div>
            <div className="col">
              <img src={pil} className="wavetwo"></img>
            </div>
          </div>
          <select
            className="dropdown"
            value={this.props.selectedData}
            onChange={(e) => this.props.onDataChange(e)}
          >
            <option value="Choose data..." disabled selected>
              Choose data...
            </option>
            <option value="CO2Emission">CO2 emission</option>
            <option value="GlobalTemperature">Global temperature</option>
            <option value="GlacierSize">Glacier size</option>
            <option value="SeaLevel">Sea level</option>
          </select>
        </label>
        <div className="inputext">
          <label htmlFor="Year from">Enter Year From: </label>
          <input
            type="text"
            placeholder="Year From..."
            className="w-100"
            id="Year from"
            onChange={(e) => this.handleYearFrom(e)}
          ></input>
        </div>
        <div className="inputext">
          <label htmlFor="Year to">Enter Year To: </label>
          <input
            type="text"
            placeholder="Year To..."
            className="w-100"
            id="Year to"
            onChange={(e) => this.handleYearTo(e)}
          ></input>
        </div>

        <button
          type="button"
          className="filter-btn"
          onClick={() =>
            this.props.onSendYears(this.state.yearFrom, this.state.yearTo)
          }
        >
          Compare years
        </button>
      </div>
    );
  }
}

export default Filter;
