import React, { Component } from "react";

class SeaLevelTable extends Component {
  render() {
    let SeaLevelData = this.props.SeaLevel;

    /*Skapar en tabell med 3 kolumner som kommer innehålla data från API. 
    Vi hämtar och mappar datan, och lägger sedan in den hämtade datan 
    i den skapade tabellen*/

    /*Skapar även en informationsruta som innehåller förklaringar och fakta
    om den hämtade datan*/
    return (
      <div>
        <table className="table table-bordered table-responsive">
          <thead>
            <tr>
              <th scope="col">Date</th>
              <th scope="col">GMSL</th>
              <th scope="col">GMSL uncertainty</th>
            </tr>
          </thead>
          <tbody>
            {SeaLevelData.map((sea) => (
              <tr key={sea.Time}>
                <td>{sea.Time}</td>
                <td>{sea["GMSL"]}</td>
                <td>{sea["GMSL uncertainty"]}</td>
              </tr>
            ))}
          </tbody>
        </table>

        <div className="tableinfo">
        <p className="titletable">What does it mean?</p>
        <p className="unit"><b>Unit:</b> Millimetre</p>
          <p><b>GMSL:</b> An abbreviation for Global Mean Sea Level.</p>
          <p><b>GMSL uncertainty:</b> Different groups record relistic uncertainty of GMSL with climate change indicator for studies such as estimating current rate and acceleration of sea level. The mean is measured in millimetre.</p>
        </div>
      </div>
    );
  }
}

export default SeaLevelTable;
