import React, { Component } from "react";
class GlacierSizeTable extends Component {
  render() {
        // sätter props med GlacierSize api på en varibel.
    let GlacierSizeData = this.props.GlacierSize;
    return (
        /*Skapar en tabell med 3 kolumner som kommer innehålla data från API. 
    Vi hämtar och mappar datan, och lägger sedan in den hämtade datan 
    i den skapade tabellen*/

    /*Skapar även en informationsruta som innehåller förklaringar och fakta
    om den hämtade datan*/
      <div>
        <table className="table table-bordered table-responsive">
          <thead>
            <tr>
              <th scope="col">Year</th>
              <th scope="col">Mean cumulative mass balance</th>
              <th scope="col">Number of observations</th>
            </tr>
          </thead>
          <tbody>
            {GlacierSizeData.map((glacier) => (
              <tr key={glacier.Year}>
                <td>{glacier.Year}</td>
                <td>{glacier["Mean cumulative mass balance"]}</td>
                <td>{glacier["Number of observations"]}</td>
              </tr>
            ))}
          </tbody>
        </table>
        <div className="tableinfo">
        <p className="titletable">What does it mean?</p>
        <p className="unit"><b>Unit: </b> Metre</p>
          <p><b>Mean cumulative mass balance:</b> The Cumulative mass balance is the mass of the glacier at a stated time, relative to its mass at some earlier time.</p>
          <p><b>Number of observations:</b> Number of observations is the total observations of the mass of the glacier during a given time period.  </p>
        </div>
      </div>
    );
  }
}

export default GlacierSizeTable;
